.form{
    padding-top: 100px;
    background-color: var(--color--bg);
    .form__content{
        display: flex;
    }
    &__left{
        padding-right: 54px;
    }
    &__title{
        font-weight: bold;
        font-size: 40px;
        line-height: 130%;
        color: var(--color-white);
    }
    &__text{
        font-weight: 600;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-white);
        opacity: 0.7;
        width:57%;
    }
    &__img{
        margin-top: 47px;
        max-width: 150%;
    }
}