.info{
    background-color: var(--color--bg);
    margin: 0 auto;
    padding-top: 170px;
    &__item{
        display: flex;
        &__img{
           
        }
    }
    &__content{
        border-radius: 10px;
        text-align: center;  
        background-color: #0d0e10;
        margin-right: 15px;
        margin-left: 3px;
        width: 362px;
        height: 389px;
        margin-bottom:50px;
        cursor: pointer;
    
        }
        &__title{
            color: var(--color-white);
            opacity: 0.7;
            font-weight: bold;
            font-size: 25px;
            line-height: 31px;
        }
        &__text{
            color: var(--color-white);
            opacity: 0.7;
            font-weight: 600;
            font-size: 16px;
            line-height: 160%;
        }
    }

    .info__img{
        display: flex;
        justify-content: center;
        margin-top: 30px;
    }

    .info__images-1{
        margin-top: 34px;
    }

  

    