.footer{
    background-color: var(--color--bg);
    padding-top: 86px;
    padding-bottom: 146px;
    &__text{
        color: var(--color-white);
        font: var(--middle-high-bold);
        opacity: 0.6;
        padding-bottom: 36px;
    }
    &__email{
        font: var(--middle-high);
        color: var(--color-white);
        padding-bottom: 20px;
        position: relative;
        &::before{
            content: '';
            background-image: url('../img/email.svg');
            width: 13px;
            height: 10.4px;
            position: absolute;
            background-repeat: no-repeat;
            background-size: cover;
            top: 4px;
            left: -18px;
        }
    }
    &__phone{
        font: var(--middle-high);
        color: var(--color-white);
        position: relative;
        &::before{
            content: '';
            background-image: url('../img/phone.svg');
            width: 13px;
            height: 12.4px;
            position: absolute;
            background-repeat: no-repeat;
            background-size: cover;
            top: 4px;
            left: -18px;
        }
    }
    &__map{
        font: var(--middle-high);
        color: var(--color-white);
        padding-top: 20px;
        position: relative;
        &::before{
            content: '';
            background-image: url(../img/map.svg);
            width: 12px;
            height: 14.4px;
            position: absolute;
            background-repeat: no-repeat;
            background-size: cover;
            top: 22px;
            left: -19px;
        }
    }
}