.hero{
    background-color: var(--color--bg);
    padding-top: 160px;
    &__strong{
        color: var(--color-indigo); 
        font-weight: 600;
        font-size: 18px;
        line-height: 160%;
    }
    &__text{
        color: var(--color-white);  
        padding-bottom: 40px;  
        font-weight: 600;
        font-size: 18px;
        line-height: 160%;
        opacity: 0.7;
    }
    &__inner{
        margin-top: 115px;
        display: flex;
    }
    &__item{
        margin-right: 43px;
    }
    &__title{
        padding-bottom: 40px;
    }
    &__img{
        padding-left: 114px;
    }
    &__btn-st{
        position: relative;
        padding: 10px 20px;
        background: var(--color-indigo);
        border-radius: 5px;
        color: var(--color-white);
        font-size: 13px;
        font-weight: 800;
        margin: 0 26px;
        margin-left: 0;
    }
    &__btn-video{
        position: relative;
        padding: 10px 36px;
        background: var(--color-indigo);
        border-radius: 5px;
        color: var(--color-white);
        font-size: 13px;
        font-weight: 800;
        margin: 0 5px;
    }
}
.hero__btn-video::before{
        content: "";
        position: absolute;
        left: -2%;
        top: 20%;
        width: 17px;
        height: 21px;
        margin-left: 15px;
        background-image: url('../img/video.svg');
        background-repeat: no-repeat;
        background-size: cover;     
}
