.learn{
    margin: 0 auto;
    background-color: var(--color--bg);
    padding-top: 180px;
    &__item{
        display: flex;
    }
    &__info{
        padding-top: 60px;
        padding-left: 164px;
    }
    &__title{
        white-space: break-spaces;
        padding-bottom: 30px;
    }
    &__text{
        padding-bottom: 50px;
        font-weight: 600;
        font-size: 16px;
        line-height: 160%;
        color: var(--color-white);
        opacity: 0.7;
        white-space: break-spaces;
    }
    &__btn{
        width: 150px;
        height: 52px;
        padding: 15px 30px;
        color: var(--color-white);
        background-color: var(--color-indigo);
        border-radius: 5px;
    }
}