.nav{
    display: flex;
    align-self: center;
    &__list{
        display: flex;
        padding-left: 83px;
        padding-right: 154px;
        padding-top: 16px;

    }
    &__link{
        color: var(--color-white);
        margin-right: 40px;
        font-weight: 600;
        font-size: 16px;
        line-height: 22px;
    }
}

.btn__header{
    background:var(--color-indigo);
    border-radius: 5px;
    padding: 15px 30px;
    color: var(--color-white);
    height: 52px;
    white-space: nowrap;
}

.menu__btn{
    order: 1;
    width: 40px;
    height: 30px;
    display: none;
    cursor: pointer;
    &::after,&::before{
        content: '';
        position: absolute;
        width: 40px;
        height: 2px;
        background-color: var(--color-white);
        left: 0;
    }
    span {
        display: block;
        height: 2px;
        width: 100%;
        background-color: var(--color-white);
        position: relative;
        left: 0;
        top: 14px;
    }
}

.menu__btn::before{ 
     top: 0;
    }


.menu__btn::after{
    bottom: 0;
}
