@media screen and (max-width:1611px) {
    .form .form__content{
        display: block;
    }
    
}


@media screen and (max-width:1240px) {
    .nav__list{
        padding-right: 0;
    }
    .learn__item{
        display: block;
    }
    .learn__info{
        padding-left:0px;
    }
    .hero{
        padding-top: 33px;
    }
    
    
}

@media screen and (max-width:1200px) {
    .hero__inner{
        display: block;
    }
    .hero__item{
        margin-bottom:15px;
    }
    
}


@media screen and (max-width:1060px) {
    .btn__header{
        white-space: pre;
        margin-left: auto;
        margin-right: auto;
    }
    .nav__list{
        position: fixed;
        right: -100%;
        top: 98px;
        height: 100vh;
        margin-right: 0;
        background-color: #004cff;
        padding: 60px;
        transition: .3s ease-in;
        display: block;
        z-index: 1;
        width: 100%;
    }

    .nav__list.nav__list--active{
        right: 0;
    }
    .nav__link{
        display: block;
        padding: 10px 0;
    }
    .menu__btn{
        display: block;
        position: absolute;
        right: 10px;
        top: 25px;
    }
    .hero__content{
        display: block;
    }
    .hero__img{
        padding-left: 0;    
        padding-top: 30px;
    }
    .hero__title{
        color: var(--color-white);
        font-weight: 800;
        font-size: 31px;
        line-height: 124%;
        padding-bottom: 12px;
    }
    .form__img{
        width: 50%;
    }
    .form{
        padding-top: 2px;
    }
    
    
}


@media screen and (max-width:855px) {
    .form__title{
    font-size: 33px;
    }
    
}


@media screen and (max-width:582px) {
    .form__text{
        font-size: 15px;
        width: 100%;
    }
    .footer__social{
        margin-left: 20px;
    }
    .btn__header{
        display: none;
    }
}

@media screen and (max-width:471px){
    .learn__title{
        padding-bottom: 13px;
        font-size: 35px;
    }
    .menu__btn{
        width: 40px;
        height: 27px;
    }
}


@media screen and (max-width:400px) {
    .hero__btn{
        display: flex;
    }
    .header__logo{ 
        width: 202px;
    }
    
}


@media screen and (max-width:320px) {
    .info__title{
        font-size: 21px;
        line-height: 31px;
    }
    .info__text{
        font-size: 13px;
    }
    .learn__title{
            padding-bottom: 13px;
            font-size: 29px;
    }
    .learn{
        padding-top: 95px;
    }
    
}